<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Transfer
      </div>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table :headers="headers" :items="data.data" class="elevation-1" loading-text="Loading... Please wait" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.date`]="{ item }">
            {{ toDate(item.date) }}
        </template>
        <template #[`item.nominal`]="{ item }">
            {{ toRupiah(item.nominal > 0 ? item.nominal : '0') }}
        </template>
        <template #[`item.status`]="{ item }">
          <v-chip small
            class="ma-2"
            :color="getColorStatus(item.status)"
            text-color="white"
          >
            {{ item.status }}
          </v-chip>
        </template>
        <template #[`item.paid`]="{ item }">
          <v-btn  :disabled="item.status === 'FUN'? true: false" color="success" @click="paid(item.id)">
            PAID
          </v-btn>
        </template>
        <template #[`item.download`]="{ item }">
          <v-btn color="primary" @click="download(item)">
            <v-icon>mdi-download</v-icon>
              Download Excel
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <detailBlog/>
    <editBlog/>
    <createBlog/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import detailBlog from '@/components/Blog/Detail.vue'
import editBlog from '@/components/Blog/Update.vue'
import createBlog from '@/components/Blog/Create.vue'
import Utils from '@/utils/util'
import store from '@/store'
export default {
  name: 'PreterminationPage',
  data: () => ({
    search: '',
    headers: [
      {
        text: '#',
        align: 'start',
        sortable: false,
        value: 'index'
      },
      {
        text: 'Tanggal',
        align: 'start',
        sortable: false,
        value: 'date'
      },
      { text: 'No. Batching', sortable: false, value: 'pvcode' },
      { text: 'Total Account', sortable: false, value: 'jumlahtransaksi', align: 'center' },
      { text: 'Total Amount', sortable: false, value: 'nominal', align: 'right' },
      { text: 'Status', sortable: false, value: 'status', align: 'center' },
      { text: 'Action', sortable: false, value: 'paid' },
      { text: '', sortable: false, value: 'download' }
    ]
  }),
  components: {
    detailBlog, editBlog, createBlog
  },
  mounted: function () {
    store.dispatch('transfer/reqData')
  },
  computed: {
    ...mapState('transfer', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('transfer', [
      'paid'
    ]),
    toDate (tanggal) {
      return Utils.toDate(tanggal)
    },
    toRupiah (nominal) {
      return Utils.toRupiahString(nominal)
    },
    checkStatus (status) {
      if (status === 'PT2' || status === 'PT3' || status === 'PAI' || status === 'RJT') {
        return ''
      }
    },
    checkView (status) {
      if (status !== 'PT3') {
        return ''
      }
    },
    getColorStatus (status) {
      if (status === 'NEW') {
        return 'success'
      } else if (status === 'CNCL' || status === 'RJT') {
        return 'red'
      } else if (status === 'APV') {
        return 'primary'
      } else {
        return 'orange'
      }
    }
  }
}
</script>
